import axios from "axios";
import { createContext, useContext, useState } from "react";
import { useNavigate } from "react-router-dom";
import { API } from "../helpers/const";

const AuthContext = createContext();

export const useAuth = () => useContext(AuthContext);

const AuthContextProvider = ({ children }) => {
  const [currentUser, setCurrentUser] = useState(null);
  const navigate = useNavigate();

  //  
  const handleRegister = async (formData) => {
    try {
      await axios.post(`${API}/user/register/`, formData);
      navigate("/login");
    } catch (error) {
      console.error(" :", error);
    }
  };

  //  
  const handleLogin = async (formData, email) => {
    try {
      const { data } = await axios.post(`${API}/user/login/`, formData);
      localStorage.setItem("tokens", JSON.stringify(data));
      localStorage.setItem("email", JSON.stringify(email));
      setCurrentUser(email);
      navigate("/");
    } catch (error) {
      console.error(" :", error);
    }
  };

  //   
  // const checkAuth = async () => {
  //   try {
  //     const tokens = JSON.parse(localStorage.getItem("tokens"));
  //     const { data } = await axios.post(`${API}/user/refresh/`, {
  //       refresh: tokens.refresh,
  //     });
  //     localStorage.setItem(
  //       "tokens",
  //       JSON.stringify({ access: data.access, refresh: tokens.refresh })
  //     );
  //     const email = JSON.parse(localStorage.getItem("email"));
  //     setCurrentUser(email);
  //   } catch (error) {
  //     console.error("  :", error);
  //   }
  // };
  const checkAuth = async () => {
    try {
      const tokens = JSON.parse(localStorage.getItem("tokens"));
      console.log("  localStorage:", tokens);

      if (!tokens || !tokens.refresh) {
        throw new Error("   ");
      }

      console.log("    ...");
      const { data } = await axios.post(`${API}/user/refresh/`, {
        refresh: tokens.refresh,
      });

      console.log(" :", data);
      localStorage.setItem(
        "tokens",
        JSON.stringify({ access: data.access, refresh: tokens.refresh })
      );

      const email = JSON.parse(localStorage.getItem("email"));
      console.log(" :", email);
      setCurrentUser(email);
    } catch (error) {
      console.error("  :", error);
    }
  };

  //    
  checkAuth();

  //  
  const handleLogOut = () => {
    localStorage.clear("tokens");
    localStorage.clear("email");
    setCurrentUser(null);
    navigate("/login");
  };

  //   
  const handlePasswordReset = async (emailOrLogin) => {
    try {
      await axios.post(`${API}/user/forgot_password/`, { email: emailOrLogin });
      console.log(`Requesting password reset for: ${emailOrLogin}`);
    } catch (error) {
      console.error("  :", error);
      throw new Error(
        "  . ,  ."
      );
    }
  };

  const values = {
    handleRegister,
    handleLogin,
    currentUser,
    checkAuth,
    handleLogOut,
    handlePasswordReset,
  };

  return <AuthContext.Provider value={values}>{children}</AuthContext.Provider>;
};

export default AuthContextProvider;
